<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品态度</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper" >
            <span class="title">送达时间 </span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect @ratingtype-select="select" @content-toggle="toggle" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="ratings"></ratingselect>
      <div class="rating-wrapper">
        <ul>
          <li v-show="needShow(rating.rateType,rating.text)"  class="rating-header border-1px" v-for="rating in ratings" :key="rating.id">
            <div class="avatar">
              <img :src="rating.avatar" width="28" height="28" >
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <star class="star" :size="24" :score="rating.score"></star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend.length">
                <span class="icon-thumb_up "></span>
                <span class="rathing-recommend">{{rating.recommend}}</span>
              </div>
              <div class="time"></div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import star from '../star/star.vue'
import ratingselect from '../ratingselect/ratingselect.vue'
import split from '../split/split.vue'
const ALL = 2
const ERR_OK = 0
export default{
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      ratings: [],
      showFlag: false,
      selectType: ALL,
      onlyContent: true
    }
  },
  created () {
    // this.$http.get('/api/ratings').then((response) => {
    //   response = response.body
    //   if (response.errno === ERR_OK) {
    //     this.ratings = response.data
    //     this.$nextTick(() => {
    //       this.scroll = new BScroll(this.$refs.ratings, {
    //         click: true
    //       })
    //     })
    //   }
    // })
    this.$http.get('https://raw.githubusercontent.com/hzf0799/hzf0799.github.io/master/data.json').then((response) => {
      response = response.body
      this.ratings = response.ratings
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.ratings, {
          click: true
        })
      })
    })
  },
  methods: {
    select (type) {
      this.selectType = type
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    toggle (onlyContent) {
      this.onlyContent = onlyContent
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    needShow (type, text) {
    if (this.onlyContent && !text) {
      return false
    }
    if (this.selectType === ALL) {
      return true
    } else {
      return type === this.selectType
    }
  }
  },
  components: {
    'star': star,
    'split': split,
    'ratingselect': ratingselect
  }
}
</script>
<style lang="stylus">
@import "../../common/stylus/mixin.styl";
.ratings
  position :absolute;
  top:174px;
  bottom :0;
  left :0;
  width :100%;
  overflow :hidden;
  .overview
    display :flex;
    padding :18px 0;
    .overview-left
      flex: 0 0 137px;
      width :137px;
      border-right :1px solid rgba(7,17,27,0.1)
      text-align :center;
      @media only screen and (max-width :320px) {
        flex: 0 0 110px;
        width :110px;
      }
      .score
        margin :7px 0;
        line-height :24px;
        font-size :24px;
        color :rgb(255,153,0);
      .title
        margin-bottom :6px;
        font-size :12px;
        color :rgb(7, 17, 27)
        line-height :12px;
        padding :6px 0;
      .rank
        font-size :10px;
        color :rgb(147, 153, 159)
        line-height :10px;
    .overview-right
      flex:1;
      padding: 6px 0 6px 24px;
      @media only screen and (max-width :320px) {
        padding-left : 5px;
      }
      .score-wrapper
        margin-bottom:8px;
        line-height :18px;
        font-size:0;
        .title
          display :inline-block;
          font-size :12px;
          line-height : 18px;
          color :rgb(7, 17, 27);
          vertical-align :top;
        .star
          display :inline-block;
          line-height : 18px;
          vertical-align :top;
          margin :0 12px;
        .score
          display :inline-block;
          vertical-align :top;
          line-height : 18px;
          font-size :12px;
          color :rgb(7,17,27);
      .delivery-wrapper
        font-size :0;
        .title
          font-size :12px;
          line-height : 18px;
          color :rgb(7, 17, 27);
        .delivery
          margin-left :12px;
          font-size :12px;
          color :rgb(147, 153, 159)
  .rating-wrapper
    padding : 0 18px;
    .rating-header
      display :flex;
      padding : 18px ;
      border-1px :(rgba(7, 17, 27, 0.1))
    .avatar
      flex: 0 0 28px;
      width :28px;
      margin-right :12px;
      img
        border-radius :50%;
  .content
    position :relative;
    flex:1;
    .name
      margin-bottom :4px;
      line-height :12px;
      font-size:10px;
      color :rgb(7, 17 ,27)
    .star-wrapper
      margin-bottom :6px;
      font-size :0;
      .star
        display :inline-block;
        margin-right :6px;
        vertical-align :top;
      .delivery
        display :inline-block;
        font-size:10px;
        line-height :10px;
        color :rgb(147, 153, 159)
        vertical-align :top;
    .text
      margin-bottom :8px;
      color :rgb(7, 17, 25);
      line-height :18px;
      font-size :12px;
    .recommend
      line-height :16px;
      .icon-thumb_up, .rathing-recommend
        display :inline-block;
        margin : 0 8px 4px 0;
        font-size : 9px;
      .icon-thumb_up
        color: rgb(0, 160, 220);
      .rathing-recommend
        padding:0 6px;
        border: 1px solid rgba(7, 17, 27, 0.1);
        border-radius :1px;
        color :rgb(147, 153, 159);
        background :#fff;
</style>



