<template>
  <header class="header" >
    <div class="content-wrapper">
      <div class="content-img">
        <img :src="seller.avatar" width="64" height="64">
      </div>
      <div class="content">
        <div class="title">
        <span class="brand"></span>
        <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分送达
        </div>
        <!-- 需要加v-if来判断是否存在存在才显示 -->
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
        <div v-if="seller.supports" class="support-count" @click="showDetail">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
      <span class="bulletin-title"></span>
      <span class="bulletin-text">{{seller.bulletin}}</span>
      <span class="icon-keyboard_arrow_right"></span>
    </div>
    <div class="header-bg">
      <img :src="seller.avatar" width="100%" height="90%" >
    </div>
    <transition name="fade">
      <div v-show="detailShow" class="detail">
          <div class="detail-wrapper clearfix">
            <div class="detail-main">
              <p class="name">{{seller.name}}</p>
              <div class="star-wrapper">
                <star :size="48" :score="seller.score"></star>
              </div>
              <div class="title">
                <div class="line"></div>
                <div class="text">优惠消息</div>
                <div class="line"></div>
              </div>
              <ul v-if="seller.supports" class="supports">
                <li class="support-item" v-for="(sellerone,index) in seller.supports" :key="index">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
                <!-- description为内容  -->
                </li>
              </ul>
              <div class="title">
                <div class="line"></div>
                <div class="text">商家消息</div>
                <div class="line"></div>
              </div>
              <div class="bulletin">
                <p class="content">{{seller.bulletin}}</p>
              </div>
            </div>
          </div>
          <div class="detail-close" @click="closeDetail">
            <i class="icon-close"></i>
          </div>
      </div>
    </transition>
  </header>
</template>
<script type="text/ecmascript-6">
  import star from '../star/star.vue'
  export default{
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        detailShow: false
      }
    },
    methods: {
      showDetail () {
        this.detailShow = true
      },
      closeDetail () {
        this.detailShow = false
      }
    },
    created () {
      this.classMap = [ 'decrease', 'discount', 'special', 'invoice', 'guarantee' ]
    },
    components: {
        'star': star
    }
  }
</script>
<style rel="stylesheet" lang="stylus">
.fade-enter-active, .fade-leave-active
  transition: all .5s;
.fade-enter,.fade-leave-to
  opacity :0;
  background :rgba(7,17,27,0);
@import "../../common/stylus/mixin.styl"
  .header
    width :100%;
    color :#fff;
    overflow :hidden;
    background :rgba(7,17,27,0.5);
    .content-wrapper
      padding :24px 12px 18px 24px;
      position : relative;
      // 去缝隙
      font-size:0;
      .content-img
        display :inline-block;
        vertical-align : top ;
        img
          border-radius :  2px;
      .content
        display :inline-block;
        font-size :14px;
        margin-left :16px;
        .title
          margin :2px 0 8px 0;
          .brand
            width :30px;
            height :18px;
            display :inline-block;
            vertical-align : top;//对齐
            bg-image :('brand');
            background-size : 30px 18px;
            background-repeat :no-repeat;
          .name
            margin-left :6px;
            font-size :16px;
            line-height :18px;
            font-weight :bold;
        .description
          margin-bottom : 10px;
          line-height : 12px;
          font-size : 12px;
        .support
          .icon
            display : inline-block;
            vertical-align :top;
            width : 12px;
            height : 12px;
            margin-right : 4px;
            background-size :12px 12px;
            background-repeat : no-repeat;
            &.decrease
              bg-image : ('decrease_1');
            &.discount
              bg-image : ('discount_1');
            &.guarantee
              bg-image : ('guarantee_1');
            &.invoice
              bg-image : ('invoice_1');
            &.special
              bg-image : ('special_1');
          .text
            line-height : 12px;
            font-size : 10px;
      .support-count
        position : absolute;
        right : 12px;
        bottom : 18px;
        padding : 0 8px;
        height : 24px;
        line-height : 24px;
        border-radius :14px;
        background : rgba(0,0,0,0.2);
        text-align : center;
        .count
          vertical-align :top;
          font-size :10px;
        .icon-keyboard_arrow_right
          margin-left:2px;
          line-height: 24px;
          font-size :10px;
          vertical-align :top;
    .bulletin-wrapper
      position :relative;
      padding :0 22px 0 12px;
      height :28px;
      line-height :28px;
      overflow :hidden;
      white-space :nowrap;
      text-overflow :ellipsis;
      background :rgba(7,17,27,0.2);
      .bulletin-title
        display :inline-block;
        vertical-align :top;
        width :22px;
        height :12px;
        margin-top :7px;
        margin-left :7px;
        bg-image:('bulletin');
        background-size:22px 12px;
        background-repeat:no-repeat;
      .bulletin-text
        vertical-align :top;
        margin :0 4px;
        font-size:10px;
      .icon-keyboard_arrow_right
        position :absolute;
        font-size :10px;
        right :12px;
        top : 8px;
    .header-bg
      position :absolute;
      top:0;
      left :0;
      width :100%;
      height :134px;
      z-index:-1;
      // 模糊
      filter :blur(10px);
    .detail
      position :fixed;
      z-index:1000;
      width :100%;
      height :100%;
      top: 0;
      left :0;
      background :rgba(7,17,27,0.8);
      .detail-wrapper
        min-height :100%;
        width :100%;
        .detail-main
          margin-top :64px;
          padding-bottom :64px;
          @media only screen and (max-width :320px) {
            margin-top :32px;
          }
          .name
            line-height :16px;
            text-align :center;
            font-size :16px;
            font-weight :700;
          .star-wrapper
            margin-top : 18px;
            padding : 2px 0;
            text-align :center;
          .title
            display :flex;
            width :80%;
            margin :30px auto 24px auto ;
            .line
              flex:1;
              top: -6px;
              position :relative;
              border-bottom :1px solid rgba(255,255,255,0.2);
            .text
              padding :0 12px;
              font-size: 14px;
          .supports
            width :80%;
            margin : 0 auto ;
            @media only screen and (max-width :320px) {
              width :90%;
            }
            .support-item
              padding : 0 12px;
              margin-bottom :12px;
              font-size :0;
              &:last-child{
                margin-bottom:0;
              }
              .icon
                margin: 0 12px;
                display :inline-block;
                width :16px;
                height :16px;
                vertical-align :top;
                background-size:16px 16px;
                background-repeat:no-repeat;
                &.decrease
                  bg-image : ('decrease_2');
                &.discount
                  bg-image : ('discount_2');
                &.guarantee
                  bg-image : ('guarantee_2');
                &.invoice
                  bg-image : ('invoice_2');
                &.special
                  bg-image : ('special_2');
              .text
                line-height :16px;
                font-size :12px;
          .bulletin
            width :80%;
            margin: 0 auto;
            .content
              padding :0 12px;
              line-height :24px;
              font-size : 12px;
              @media only screen and (max-width :320px) {
                padding :0;
                font-size :10px;
              }
      .detail-close
        position :relative;
        width :32px;
        height :32px;
        margin :-64px auto 0 auto;
        clear :both;
        font-size :32px;
</style>



