<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc border-1px">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>
            元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>
              元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>
            元
            </div>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="(sellerone,index) in seller.supports" :key="index">
          <span class="icon" :class="classMap[seller.supports[index].type]"></span>
          <span class="text">{{seller.supports[index].description}}</span>
          <!-- description为内容  -->
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="wrapper">
          <ul class="pic-list" ref="list">
            <li class="pic-item" v-for="pic in seller.pics" :key="pic.id">
              <img :src="pic" width="120" height="90" >
            </li>
          </ul>
        </div>
      </div>
       <split></split>
       <div class="info">
         <h1 class="title border-1px">商家信息</h1>
         <ul>
           <li class="info-item" v-for="info in seller.infos" :key="info">{{info}}</li>
         </ul>
       </div>
      </div>
  </div>
</template>
<script type="text/ecmascript-6">
import star from '../star/star.vue'
import split from '../split/split.vue'
import BScroll from 'better-scroll'

  export default{
    props: {
      seller: {
        type: Object
      }
    },
    components: {
      'star': star,
      'split': split
    },
    watch: {
      'seller' () {
        this._initScroll(),
        this._initPics()
      }
    },
    mounted () {
      // 这里不增加会导致tab完不会滚动
      this._initScroll(),
      this._initPics()
    },
    methods: {
      _initScroll () {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.seller, {
        click: true
        })
        } else {
          this.scroll.refrush()
        }
      },
      _initPics () {
        if (!this.picScroll) {
          if (this.seller.pics) {
            let picWidth = 120
            let margin = 6
            let width = (picWidth + margin) * this.seller.pics.length
            this.$refs.list.style.width = width
            this.$nextTick(() => {
              this.picScroll = new BScroll(this.$refs.wrapper, {
                scrollX: true,
                eventPassthrough: 'vertical'
              })
            })
          }
        } else {
        this.picScroll.refrush()
      }
      }
    },
    created () {
      this.classMap = [ 'decrease', 'discount', 'special', 'invoice', 'guarantee' ]
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus" >
@import "../../common/stylus/mixin.styl"
.seller
  position :absolute;
  top :174px;
  bottom :0;
  left :0;
  width :100%;
  overflow :hidden;
  .overview
    padding:18px;
    .title
      margin-bottom : 8px;
      line-height :14px;
      color :rgb(7, 17, 27);
      font-size: 14px;
    .desc
      padding-bottom :18px;
      line-height :18px;
      border-1px: (rgba(7, 17, 27, 0.1));
      font-size : 0;
      .star
        display :inline-block;
        margin-right :8px;
        vertical-align :top;
      .text
        display :inline-block;
        margin-right:  12px;
        line-height :18px;
        vertical-align :top;
        font-size :10px;
        color :rgb(77, 85, 93);
    .remark
      display :flex;
      padding-top : 18px;
      .block
        flex :1;
        text-align :center;
        border-right :1px soild rgba(7, 17, 27, 0.1);
        &.last-child
          border :none;
        h2
          margin-bottom :4px;
          line-height :10px;
          font-size :10px;
          color :rgb(147, 153, 159);
        .content
          line-height :24px;
          font-size :10px;
          color :rgb(7, 17, 27);
          .stress
            font-size :24px;
  .bulletin
    padding: 18px 18px 0 18px;
    .title
      margin-bottom : 8px;
      line-height :14px;
      color :rgb(7, 17, 27);
      font-size :14px;
    .content-wrapper
      padding : 0 12px 16px 12px;
      border-1px :(rgba(7, 17, 20, 0.1));
      .content
        line-height :24px;
        font-size: 12px;
        color :rgb(240, 20, 20);
    .supports
      .support-item
        padding: 16px 12px;
        border-1px :(rgba(7, 17, 20, 0.1));
        font-size :0;
        &:last-child
          border-none :();
        .icon
          display : inline-block;
          vertical-align :top;
          width : 32px;
          height : 32px;
          margin-right : 4px;
          background-size :12px 12px;
          background-repeat : no-repeat;
          &.decrease
            bg-image : ('decrease_4');
          &.discount
            bg-image : ('discount_4');
          &.guarantee
            bg-image : ('guarantee_4');
          &.invoice
            bg-image : ('invoice_4');
          &.special
            bg-image : ('special_4');
        .text
          font-size :12px;
          line-height :16px;
  .pics
    padding :18px
    .title
      margin-bottom : 8px;
      line-height :14px;
      color :rgb(7, 17, 27);
      font-size: 14px;
    .pic-wrapper
      width :100%;
      overflow: hidden;
      white-space : nowrap;
      .pic-list
        font-size:0;
        .pic-item
          display :inline-block;
          margin-right :6px;
          width :120px;
          height:90px;
        &:last-child
          margin-right :0;
  .info
    padding: 18px 18px 0 18px;
    .title
      padding-bottom :12px;
      line-height :14px;
      border-1px(rgba(7, 17, 27, 0.1));
      color:rgb(7, 17, 27);
      font-size: 14px
    .info-item
      padding : 16px 12px;
      line-height :16px;
      border-1px(rgba(7, 17, 27, 0.1));
</style>



